export default {
    state: {
        provider: null,
        signer: null,
        account: null
    },
    mutations: {
        SET_WALLET(state, { provider, signer, account }) {
            state.provider = provider;
            state.signer = signer;
            state.account = account;
        }
    },
    getters: {
        getProvider: (state) => state.provider,
        getSigner: (state) => state.signer || state.provider,
        getAccount: (state) => state.account
    },
};